.card-panel {
	max-width: 550px;
	margin: 60px auto 0;
}
.row-custom {
	margin-bottom: 44px;
}
.btn-custom {
	display: inline-block;
	position: relative;
	font-size: 22px;
	text-align: center;
	line-height: 44px;
	text-decoration: none;
	color: white;
	background-color: #6135b4;
	border-radius: 4px;
	min-width: 124px;
	margin: 50px auto 0;
	min-height: 44px;
	padding: 0 10px;
	top: -22px;
	text-shadow: 0 -1px -1px #5e35b1;
	-webkit-box-shadow: 0 4px 0 #492e92, 0 5px 5px 1px rgba(0, 0, 0, 0.4);
	        box-shadow: 0 4px 0 #492e92, 0 5px 5px 1px rgba(0, 0, 0, 0.4);
	-webkit-transition: all 0.15s ease-in-out;
	transition: all 0.15s ease-in-out;
}
.btn-custom:hover {
	background-color: #6635b9;
	text-shadow: 0 -1px -1px #5e35b1;
	-webkit-box-shadow: 0 4px 0 #492e92, 0 5px 5px 1px rgba(0, 0, 0, 0.4);
	        box-shadow: 0 4px 0 #492e92, 0 5px 5px 1px rgba(0, 0, 0, 0.4);
}
.btn-custom:active {
	top: -18px;
	-webkit-box-shadow: none;
	        box-shadow: none;
}
.btn-custom:disabled {
	background-color: #51329b !important;
	color: #ffffff !important;
	-webkit-box-shadow: 0 4px 0 #3a2e73, 0 5px 5px 1px rgba(0, 0, 0, 0.4);
	        box-shadow: 0 4px 0 #3a2e73, 0 5px 5px 1px rgba(0, 0, 0, 0.4);

	/*top: -18px;*/
	/*box-shadow: none;*/
}

.modal-mask {
	position: fixed;
	z-index: 9998;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0, 0, 0, .5);
	display: table;
	-webkit-transition: opacity .3s ease;
	transition: opacity .3s ease;
}
.modal-wrapper {
	display: table-cell;
	vertical-align: middle;
	position: relative;
}
.modal-container {
	position: relative;
	max-width: 600px;
	width: 100%;
	margin: 0px auto;
	padding: 20px 30px;
	background-color: #fff;
	border-radius: 2px;
	-webkit-box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
	        box-shadow: 0 2px 8px rgba(0, 0, 0, .33);
	-webkit-transition: all .3s ease;
	transition: all .3s ease;
	font-family: Helvetica, Arial, sans-serif;
}
.modal-header h3 {
	font-size: 34px;
	text-align: center;
	margin-top: 0;
	color: #000;
	margin-bottom: 40px;
}
.modal-body {
	font-size: 24px;
	text-align: center;
	margin: 20px 0;
}
.modal-default-button {
	/*float: right;*/
	position: absolute;
	width: 23px;
	height: 23px;
	cursor: pointer;
	top: 19px;
	right: 19px;
}
.modal-default-button:after, .modal-default-button:before {
	width: 23px;
	height: 1px;
	margin-top: 0;
	margin-left: -11px;
	background: #003da6;
}
.modal-default-button:after {
	-webkit-transform: rotate(-45deg);
	        transform: rotate(-45deg);
}
.modal-default-button:before {
	-webkit-transform: rotate(45deg);
	        transform: rotate(45deg);
}
.modal-default-button:before, .modal-default-button:after {
	content: "";
	display: block;
	position: absolute;
	top: 50%;
	left: 50%;
	background: #555;
	height: 2px;
	width: 30px;
	margin-top: -2px;
	margin-left: -15px;
	border-radius: 10px;
}
.modal-enter {
	opacity: 0;
}
.modal-leave-active {
	opacity: 0;
}
.modal-enter .modal-container,
.modal-leave-active .modal-container {
	-webkit-transform: scale(1.1);
	transform: scale(1.1);
}

